import React from 'react';
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";

const Register = (props) => {

    const { iFirstName, iLastName, iEmail, iPassword, iConfirm, onSubmitProp } = props;

    return (
        <div >
            <Formik
                initialValues={{
                    firstName: iFirstName,
                    lastName: iLastName,
                    email: iEmail,
                    password: iPassword,
                    confirmPassword: iConfirm,
                }}
                validationSchema={Yup.object().shape({
                    firstName: Yup.string()
                        .min(3, "Tu nombre es muy corto")
                        .required("Por favor ingresa tu nombre"),

                    lastName: Yup.string()
                        .min(3, "El apellido es muy corto")
                        .required("Por favor ingrese el apellido correctamente"),

                    email: Yup.string()
                        .email("Correo no valido")
                        .min(3, "Este correo electrnico es incorrecto")
                        .required("Por favor, ingresa un correo electrnico vlido"),

                    password: Yup.string()
                        .equals([Yup.ref('confirmPassword'), null], "las contraseas no son iguales")
                        .min(8, "La clave debe contener ms de 8 caractes")
                        .required("Por favor ingrese una contrasea"),

                    confirmPassword: Yup.string()
                        .equals([Yup.ref('password'), null], "las contraseas no son iguales")
                        .min(8, "La clave debe contener ms de 8 caractes")
                        .required("Por favor ingrese la confirmacin de la contrasea"),
                })}

                onSubmit={(values, { setSubmitting }) => {
                    const timeOut = setTimeout(() => {
                        console.log(values);
                        onSubmitProp(values);
                        setSubmitting(false);
                        clearTimeout(timeOut);
                    }, 1000);
                }}
            >
                {({
                    values,
                    errors,
                    touched,
                    handleSubmit,
                    //isSubmitting,
                    //validating,
                    valid,
                }) => {
                    return (
                        <div>
                            <Form method="post" onSubmit={handleSubmit}>
                                <label htmlFor="firstName">Nombre</label>
                                <Field id='firstName' type="text" placeholder="Nombre" name='firstName' />
                                {errors.firstName && touched.firstName && <p>{errors.firstName}</p>}

                                <label htmlFor="lastName">Apellido</label>
                                <Field id='lastName' type="text" placeholder="Apellido" name='lastName' />
                                {errors.lastName && touched.lastName && <p>{errors.lastName}</p>}

                                <label htmlFor="email">Correo Electrnico</label>
                                <Field id='email' type="text" placeholder="Email" name='email' />
                                <ErrorMessage name="email">{(msg) => <p>{msg}</p>}</ErrorMessage>

                                <label htmlFor="password">Contrasea</label>
                                <Field id='password' type="password" placeholder="Contrasea" name='password' />
                                {errors.password && touched.password && <p>{errors.password}</p>}

                                <label htmlFor="confirmPassword">Confirmar Contrasea</label>
                                <Field id='confirmPassword' type="password" placeholder="Confirmar Contrasea" name='confirmPassword' />
                                {errors.confirmPassword && touched.confirmPassword && <p>{errors.confirmPassword}</p>}
                                <br></br>
                                <button type="submit" disabled={Object.values(errors).length > 0}>Registrarse</button>
                            </Form>
                        </div>
                    );
                }}
            </Formik>
        </div>
    );
}

export default Register;
